import React, { useEffect, useState } from "react";
import "./App.css";
import doors from "./json/doors.json";

function App() {
  const [openedDoors, setOpenedDoors] = useState<string[]>([]);
  const [now, setNow] = useState(new Date());
  const [modalMessage, setModalMessage] = useState<string | null>(null);

  useEffect(() => {
    const local = localStorage.getItem("isOpen");
    setOpenedDoors(local ? JSON.parse(local) : []);
  }, []);

  useEffect(() => {
    localStorage.setItem("isOpen", JSON.stringify(openedDoors));
  }, [openedDoors]);

  useEffect(() => {
    const timer = setInterval(() => setNow(new Date()), 1000);
    return () => clearInterval(timer);
  }, []);

  const parseLocalDate = (dateStr: string) => {
    const [year, month, day] = dateStr.split("-").map(Number);
    return new Date(year, month - 1, day);
  };

  const openDoorAndShowMessage = (door: { day: string; message: string }) => {
    const isAlreadyOpen = openedDoors.includes(door.day);
    if (!isAllowedToOpen(door.day)) return;

    if (!isAlreadyOpen) {
      setOpenedDoors(prev => [...prev, door.day]);
    }
    setModalMessage(door.message);
  };

  const isAllowedToOpen = (date: string) => {
    const localDateMidnight = new Date();
    localDateMidnight.setHours(0, 0, 0, 0);
    const targetDate = parseLocalDate(date);
    return localDateMidnight >= targetDate;
  };

  const getCountdown = (targetDate: Date) => {
    const unlockTime = new Date(targetDate);
    unlockTime.setHours(0, 0, 0, 0);
    const diff = unlockTime.getTime() - now.getTime();
    if (diff <= 0) return "Ready!";
    const hours = Math.floor(diff / 3600000);
    const minutes = Math.floor((diff % 3600000) / 60000);
    const seconds = Math.floor((diff % 60000) / 1000);
    return `${hours}h ${minutes}m ${seconds}s`;
  };

  const getDoorImage = (date: string) => {
    return `/img/doors/${date}.jpg`;
  };

  return (
    <div className="container">
      <div className="calendar">
        {doors.map((door, index) => {
          const doorDate = parseLocalDate(door.day);
          const dateString = door.day;
          const isOpen = openedDoors.includes(dateString);
          const allowed = isAllowedToOpen(dateString);
          const backgroundImage = getDoorImage(dateString);

          return (
            <div key={index} className="door-wrapper">
              <p className="door-date">{doorDate.toDateString()}</p>
              <div
                className={`door ${allowed ? "clickable" : "locked"}`}
                onClick={() => allowed && openDoorAndShowMessage(door)}
              >
                <img
                  src={backgroundImage}
                  alt="door"
                  className={`door-image ${allowed ? "" : "blurred"}`}
                  onError={(e) => {
                    (e.target as HTMLImageElement).src = "/img/calendar_backdrop.jpg";
                  }}
                />
                <div className="door-overlay">
                  {!allowed && (
                    <div className="door-front">
                      <p className="countdown">{getCountdown(doorDate)}</p>
                    </div>
                  )}
                </div>
              </div>
            </div>
          );
        })}
      </div>

      {modalMessage && (
        <div className="modal-overlay" onClick={() => setModalMessage(null)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <button className="modal-close" onClick={() => setModalMessage(null)}>Ã—</button>
            <p>{modalMessage}</p>
          </div>
        </div>
      )}
    </div>
  );
}

export default App;
